.todoBody{
  height: 100vh;
  width: 100vw;
  color: rgb(255, 255, 255);
  background-image: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url(todo.jpg);
  float: left;
}
.app-title{
  margin-top: 20vh;
  background: rgb(54, 82, 143);
  width: 30vw;
  border-radius: 10px;
  padding-top: 2vh;
  height: 8vh;
}
.inputItem{
  border: none;
  border-radius: 5px;
  text-align: center;
  height: 5vh;
}
.inputItem:focus{
  outline: none;
}
.add-btn{
  width: 3vw;
  height: 5.3vh;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  margin-left: 1vw;
}
.add-btn:hover{
  background: rgb(197, 197, 197);
  color: white;
  transition: ease-in 0.5s;
}
.add-btn:focus{
  outline: none;
}
.list{
  list-style-type: none;
  text-align: center;
  float: left;
  width: 100%;
  border-radius: 10px;
}
.listItems{
  background: rgb(151, 169, 219);
  margin-top: 1vh;
  padding-top: 2vh;
  height: 6vh;
  width: 18.3vw;
  border-radius: 10px;
  padding-left: 2vw;
  margin-left: 3vw;
}
.btn{
  margin-right: 3.3vw;
  float: right;
  border: none;
  height: 4vh;
  width: 2vw;
  border-radius: 10px;
  cursor: pointer;
}
.btn:hover{
  background: rgb(45, 76, 177);
  transition: ease-in 0.5s;
  color: white;
}
.btn:focus{
  outline: none;
}